// 
import VeCurd from './ve-curd'

// 
const components = [
    VeCurd
]

//  install  Vue  use 
const install = function (app) {
    // 
    if (install.installed) return
    // 
    components.map(component => app.component(component.name, component))
}

// 
if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue)
}

export default {
    //  install Vue.use() 
    install,
    // 
    ...components
}